body {
    font-family: "Montserrat", sans-serif;
    box-sizing: border-box;
    margin: 0 auto;
    line-height: 1.2;
    
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}
a {
    text-decoration: none;
    cursor: pointer;
}

ul {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin-top: 0;
    margin-bottom: 0;
}

ul {
    padding-left: 0;
}

.conteiner {
    padding-right: 20px;
    padding-left: 20px;
    border: 1px solid tomato;
    

    @media screen and (min-width: 480px) {
        width: 480px;
        margin-left: auto;
        margin-right: auto;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-right: 32px;
        padding-left: 32px;
    }

    @media screen and (min-width: 1200px) {
        width: 1200px;
        padding-right: 16px;
        padding-left: 16px;
    }
}

.title--hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    overflow: hidden;
}

